import React, {Component} from 'react';
import './App.css';
import "survey-react/survey.css";
import * as Survey from "survey-react";

class App extends Component {
  constructor (props){
    super(props)
    this.state = {

    }
    this.onCompleteComponent = this.onCompleteComponent.bind(this)
  }
  onCompleteComponent = () =>{
    this.setState({
      isCompleted:true
    })
  }

  render(){
    var surveyJSON = {"title":"Enter New Client","description":"Use this questionnaire to enter any new clients into the database.","pages":[{"name":"page1","elements":[{"type":"text","name":"question1","title":"Name of Client"},{"type":"text","name":"question10","title":"Age of Client"},{"type":"text","name":"question2","title":"Phone Number"},{"type":"text","name":"question5","title":"Emergency Contact"},{"type":"text","name":"question3","title":"Email Address"},{"type":"text","name":"question4","title":"Street Address"},{"type":"text","name":"question6","title":"Zip Code"},{"type":"comment","name":"question7","title":"Description of Ailment"},{"type":"radiogroup","name":"question8","title":"Type of Care","choices":[{"value":"item1","text":"Frequent"},{"value":"item2","text":"Infrequent"},{"value":"item3","text":"Need-Based"}]},{"type":"comment","name":"question9","title":"Other Important Information"}]}]};

    var surveyRender = !this.state.isCompleted ? (
      <Survey.Survey
        json={surveyJSON}
        showCompletedPage={false}
        onComplete={this.onCompleteComponent}
      />
    ) : null

    var onSurveyCompletion = this.state.isCompleted ? (
      <div> Client Added </div>
    ) :null;
  return (
    <div className="App">
      <div>
        {surveyRender}
        {onSurveyCompletion}
      </div>
    </div>
  );
}
}
export default App;
